.the-guy {
  position: relative;
  font-weight: 300;
  font-size: 16px;
  text-align: center;
}

.side-bar-header {
  height: 61px;
  /* border: 0; */
  border-radius: 0 !important;
}
.accordion-segment {
  padding: 0 !important;
  margin: 0 !important;
  .accordion .title {
    padding: 12px 0 12px 10px !important;
    font-size: 16px !important;
    color: #b3b3b3 !important;
  }
  .accordion .title.active {
    background: rgba(150, 150, 150, 0.15) !important;
  }
  .accordion .content {
    padding: 0 !important;
  }
}

.square {
  transition: all 0.13s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.segmentWrapper {
  min-height: 88vh;
  margin: 74px 10px 10px 10px !important;
  transition: all 0.1s ease;
}

.responsive-svg-container {
  max-width: 1632px; 
  max-height: 761px; 
  margin: 0px auto;
}

.visual-array{
  background-color: darken($color: #242424, $amount: 0) !important;
  width: fit-content ;
  margin: auto !important;
  color: white !important;
  opacity: 0.9;
   pre{
    text-align: center;
    font-size: 24px;
    font-weight: 300;
      .array-element {
        color: #2196f3;
      }
   }
}
